button {
  cursor: pointer;
}

.button-wrapper {
  display: contents;
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  max-width: calc(100% - 20px - 20px);
  margin: 5px;
  padding: 10px 15px;
  border-radius: var(--rounded);
  background: var(--primary);
  color: var(--text-alt);
  text-align: center;
  font: inherit;
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  text-decoration: none;
  vertical-align: middle;
  appearance: none;
  transition: background var(--transition), color var(--transition);
}

.button:hover {
  background: var(--text);
  color: var(--background);
}

.button[data-style="bare"] {
  padding: 0px;
  background: none;
  color: var(--primary);

  &:hover {
    color: var(--hover);
  }
}

.button[data-flip] {
  flex-direction: row-reverse;
}
 
/* Style for the KakaoTalk Button */
.kakao-button {
  display: inline-block;
  padding: 12px 24px;
  margin-top: 15px;
  background-color: #FEE500; /* Kakao's signature yellow */
  color: #191919;
  font-family: 'Roboto Slab', serif; /* Matches your site's font */
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border-radius: 12px;
  border: 1px solid #1919192a;
  transition: all 0.2s ease-in-out;

  &:hover {
    background-color: #f0d900;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  }
}